<template>
  <article class="services">
    <div>
      <button @click="previous()">
        <i class="fas fa-angle-left"></i>
      </button>
      <button @click="next()">
        <i class="fas fa-angle-right"></i>
      </button>
    </div>
    <div id="sections">
      <section
        v-for="(section, index) in $store.state.sections"
        :key="index"
        :data-index="index + 1"
      >
        <img :src="section.icon" :alt="section.title" class="icon" />
        <h4>{{ section.name }}</h4>
        <p>{{ section.description }}</p>
        <router-link :to="`/${section.link}`"
          >Browse project<button><i class="fas fa-angle-right"></i></button
        ></router-link>
      </section>
    </div>
  </article>
</template>

<script>
export default {
  name: "slider",
  data() {
    return {
      sections: [],
      steps: 0,
      stepAmount: 0,
    };
  },
  watch: {
    steps(newStep) {
      try {
        this.stepAmount = this.sections[newStep].offsetWidth;
        document.getElementById("sections").style.left = `-${
          this.stepAmount * this.steps
        }px`;
      } catch (e) {
        return;
      }
    },
  },
  methods: {
    next() {
      let offsetRightOfSections =
        document.getElementById("sections").offsetLeft +
        document.getElementById("sections").offsetWidth;
      if (
        window.innerWidth <=
        offsetRightOfSections + this.sections[0].offsetWidth
      ) {
        this.steps++;
      }
    },
    previous() {
      if (this.steps > 0) {
        this.steps--;
      }
    },
  },
  mounted() {
    this.sections = document.getElementById("sections").children;
    this.stepAmount = this.sections[0].offsetWidth;
  },
};
</script>

<style lang="scss" scoped>
article.services {
  background-color: #27153d;
  width: 100%;
  overflow: hidden;
  margin: 20px 0;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  div#sections {
    display: inline-flex;
    flex-flow: row nowrap;
    position: relative;
    left: 0;
    transition: all 0.2s ease-out;
    & > section {
      flex-shrink: 0;
      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-end;
      align-items: center;
      position: relative;
      padding: 160px 20px 40px;
      color: white;
      transition: background-image 0.4s ease;
      border-left: 2px solid rgba(255, 255, 255, 0.05);
      overflow: hidden;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(
          60deg,
          transparent 0%,
          rgba(255, 255, 255, 0.3) 100%
        );
        opacity: 0;
        transition: opacity 0.2s ease;
        z-index: 2;
      }
      &:hover::after {
        opacity: 1;
      }
      &::before {
        content: attr(data-index);
        position: absolute;
        top: 20px;
        left: 10px;
        z-index: 1;
      }
      img.icon {
        width: 80px;
        margin: 0 60px;
        z-index: 3;
      }
      img.poster {
        position: absolute;
        top: 0;
        right: 0;
        height: 40%;
      }
      p {
        max-width: 240px;
        min-width: 200px;
        font-size: 0.8em;
        color: var(--accent);
      }
      a {
        box-sizing: content-box;
        margin-top: 10px;
        padding: 10px 40px 10px 10px;
        font-weight: bold;
        box-shadow: 0 0 8px 2px var(--accent) inset;
        position: relative;
        font-size: 0.8em;
        z-index: 3;
        button {
          color: white;
          position: absolute;
          top: 0;
          right: 0;
          width: 30px;
          height: 100%;
          background-color: var(--accent);
          margin-left: 10px;
          border: none;
          outline: none;
          opacity: 0.6;
        }
      }
    }
  }
  div:not(#sections) {
    z-index: 4;
    display: flex;
    flex-shrink: 0;
    width: 160px;
    flex-flow: row nowrap;
    align-items: center;
    padding: 0;
    padding-left: 20px;
    background: linear-gradient(
      220.1deg,
      rgba(32, 23, 72, 1) 0%,
      rgba(75, 43, 132, 1) 100%
    );
    &::after {
      display: none;
    }
    button {
      width: 30px;
      height: 30px;
      background: none;
      padding: 10px;
      margin: 10px;
      border: 2px solid white;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      transition: all 0.1s ease;
      &:hover {
        transform: scale(1.1);
        border-color: var(--accent);
      }
    }
  }
}
@media (max-width: 1020px) {
  article.services {
    div#sections {
      flex-flow: row wrap;
      section {
        border: none;
        flex-grow: 1;
      }
    }
    div:not(#sections) {
      display: none;
    }
  }
}
</style>
